import './style.css';
import avatar from '../../assets/avatar/avatar.jpg';

const TopAbout = () => {
  return (
    <div className="topAbout">
      <img className="avatar" alt="avatar" src={avatar} />
      <div className="aboutInfo">
        <h2 className="aboutName"> </h2>
        <blockquote className="blockquote">
                 , 
          !
        </blockquote>
      </div>
    </div>
  );
};

export { TopAbout };
